@font-face {
  font-family: 'DM Sans';
  font-weight: 400;
  font-style: normal;
  src: url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.eot'); /* IE9 eot*/
  src: url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.eot?iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.woff2')
      format('woff2'),
    /* chrome、firefox */ url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.woff')
      format('woff'),
    /* chrome、firefox */ url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.ttf')
      format('truetype'),
    /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
      url('./../fonts/DM-Sans-Regular/DM-Sans-Regular.svg#DMSans-Regular')
      format('svg'); /* iOS 4.1- */
}

