:root {
  --secondary-light: #e0e0e0;
  --primary-akzent: #4f2ee8;
  --secondary-akzent: #eac645;
  --text-secondary-light: #b4afaf;
  --primary-akzent-rgba: rgba(79, 46, 232, .4);
  --time: 300ms;
  --cubic: linear;
  --switcher-bg-light: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --switcher-bg-dark: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --switcher-status: linear-gradient(180deg, #fff 0%, #f7f7f7 100%);
  --switcher-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
  --switcher-status-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0px 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.light {
  --primary-bg: #f6f6f6;
  --secondary-bg: #fff;
  --primary-color: #111;
  --secondary-color: #fff;
  --logo-color: #000;
  --list-category-accent-text: #4f2ee8;
  --list-category-text: rgba(17, 17, 17, .6);
  --scroll: #e0e0e0;
  --shopping-list-text: rgba(0, 0, 0, .6);
  --pop-up-info-text: rgba(17, 17, 17, .5);
}

.dark {
  --primary-bg: #202024;
  --secondary-bg: #111;
  --primary-color: #fff;
  --secondary-color: #111;
  --logo-color: #f3f3f3;
  --list-category-accent-text: #eac645;
  --list-category-text: rgba(255, 255, 255, .6);
  --scroll: rgba(17, 17, 17, .6);
  --shopping-list-text: rgba(255, 255, 255, .6);
  --pop-up-info-text: rgba(255, 255, 255, .5);
}

