.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.switcher-theme {
  width: 40px;
  height: 20px;
  background-image: var(--switcher-bg-light);
  box-shadow: var(--switcher-shadow);
  cursor: pointer;
  border-radius: 20px;
  padding: 1px;
  display: block;
  position: relative;
  overflow: hidden;
}

.switcher-theme:before {
  content: "";
  width: 100%;
  height: 100%;
  background-image: var(--switcher-bg-dark);
  opacity: 0;
  transition: opacity var(--time) var(--cubic);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.switcher-theme--is-checked:before {
  opacity: 1;
}

.switcher-theme__checkbox:checked + .switcher-theme__status {
  transform: translateX(20px);
}

.switcher-theme__status {
  width: 18px;
  height: 18px;
  background-image: var(--switcher-status);
  box-shadow: var(--switcher-status-shadow);
  transition: transform var(--time) var(--cubic);
  border-radius: 50%;
  transform: translateX(0);
}

