.card {
  background-color: var(--secondary-bg);
  border: 2px solid var(--primary-akzent-rgba);
  border-radius: 16px;
  gap: 14px;
  padding: 14px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .card {
    padding: 24px;
  }
}

.card__delete-btn {
  border-radius: 50%;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  .card__delete-btn {
    top: 24px;
    right: 24px;
  }
}

.delete-btn__icon {
  width: 28px;
  height: 28px;
}

@media screen and (min-width: 1440px) {
  .delete-btn__icon {
    top: 34px;
    right: 34px;
  }
}

.card__img-thumb {
  width: 100px;
  height: 142px;
  border-radius: 8px;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .card__img-thumb {
    width: 116px;
    height: 165px;
  }
}

.card__img {
  object-fit: cover;
}

.card__descr {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.card__title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 15px;
  line-height: 1.12;
}

@media screen and (min-width: 335px) {
  .card__title {
    font-size: 16px;
  }
}

.card__subtitle {
  letter-spacing: .03em;
  color: var(--text-secondary-light);
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.17;
}

.card__text {
  -webkit-line-clamp: 3;
  max-width: 193px;
  text-overflow: ellipsis;
  letter-spacing: -.04em;
  -webkit-box-orient: vertical;
  margin-bottom: 14px;
  display: -webkit-box;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .card__text {
    max-width: 508px;
  }
}

@media screen and (min-width: 1440px) {
  .card__text {
    max-width: 808px;
  }
}

.author {
  letter-spacing: -.04em;
  color: var(--text-secondary-light);
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
  display: flex;
}

@media screen and (min-width: 768px) {
  .author {
    align-items: baseline;
  }
}

.shops {
  mix-blend-mode: luminosity;
  align-items: baseline;
  gap: 4px;
  display: flex;
}

@media screen and (min-width: 335px) {
  .shops {
    gap: 8px;
  }
}

@media screen and (min-width: 768px) {
  .shops {
    align-items: center;
    gap: 16px;
  }
}

.shop__item {
  width: 16px;
  height: 16px;
  cursor: pointer;
  transition: filter var(--time) var(--cubic);
}

.shop__item--amazon {
  width: 32px;
  height: 11px;
}

.shop__item:hover, .shop__item:focus {
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, .15));
}

@media screen and (min-width: 768px) {
  .shop__item--amazon {
    width: 48px;
    height: 15px;
  }

  .shop__item--book {
    width: 28px;
    height: 27px;
  }

  .shop__item--bookshop {
    width: 32px;
    height: 30px;
  }
}

